import svg from "./demo2.svg"
import { HCF, HLocalStorage } from "@macashipo/mlib"
import { KConfigApp, KStorage } from "src/helpers/K"
export default function PageNotFound(props) {
  if (HCF.getCF(KConfigApp.autoRedirectLoginPageNotFound) === true) {
    console.warn("aaaa", props)
    if (props.location) {
      let _path = `${props.location.pathname}${props.location.search}`
      let _currentRedirect = HLocalStorage.getString(
        KStorage.redirectAfterLogin,
        ""
      )
      //kiem tra tranh bi redirect tr√πng
      console.warn("aaaaaaa", _currentRedirect, _path)
      if (_currentRedirect != _path) {
        HLocalStorage.saveString(KStorage.redirectAfterLogin, _path)
        window.location.href = "/login"
      } else {
        //neu bi trung thi remove de khong bi loop
        HLocalStorage.removeItem(KStorage.redirectAfterLogin)
      }
    }
  }
  console.warn("render page not found", props)
  return (
    <div style={{ textAlign: "center" }}>
      <img src={svg} alt="404" style={{ maxHeight: "90vh" }} />
      <div className="buttons">
        <button
          className="btn btn-secondary me-2"
          onClick={() => {
            window.history.back()
          }}
        >
          Back
        </button>
        <button
          className="btn btn-primary"
          onClick={() => {
            window.open("/", "_self")
          }}
        >
          Home
        </button>
      </div>
    </div>
  )
}
